import { UF } from './uf.class';
import { Municipio } from './municipio.class';
import { Atividade } from './atividade.class';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/share';

export class Cadastro {
    pessoal_hash         : string = '';
    pessoal_nmCliente    : string = '';
    pessoal_cdDocumento  : string = '1';
    pessoal_nuDocumento  : string = '';
    pessoal_dsOcupacao   : string = '';
    pessoal_dsOrganizacao: string = '';
    pessoal_dsCEP        : string = '';
    pessoal_dsEndereco   : string = '';
    pessoal_dsNumero     : string = '';
    pessoal_dsComplemento: string = '';
    pessoal_dsBairro     : string = '';
    pessoal_dsMunicipio  : string = '';
    pessoal_dsEstado     : string = '';
    pessoal_nuTelefone   : string = '';
    pessoal_dsEmail      : string = '';
    pessoal_cdSenha      : string = '';
    pessoal_icTermo      : boolean = false;

    produto_selecionado: number[] = [];
    alteracao_produto1_municipioTodos: Subject<string> = new Subject();
    alteracao_produto1_municipio: Subject<Municipio[]> = new Subject();
    alteracao_produto1_idVigencia: Subject<number> = new Subject();
    alteracao_produto2_ufTodos: Subject<string> = new Subject();
    alteracao_produto2_uf: Subject<UF[]> = new Subject();
    alteracao_produto2_atividadeTodos: Subject<string> = new Subject();
    alteracao_produto2_negociacaoPersonalizada: Subject<boolean> = new Subject();
    alteracao_produto2_atividade: Subject<Atividade[]> = new Subject();

    produto1_cdUf: number = null;
    produto1_cdMunicipio: number = null;
    _produto1_idVigencia: number = null;
    _produto1_municipioTodos: string = '1';
    _produto1_municipioSelecionados: Municipio[] = [];
    produto1_valor: number = 0;
    
    produto2_cdUf: number = null;
    produto2_idAtividade: number = null;
    _produto2_ufTodos: string = '1';
    _produto2_ufSelecionados: UF[] = [];
    _produto2_atividadeTodos: string = '1';
    _produto2_negociacaoPersonalizada: boolean = false;
    _produto2_atividadeSelecionados: Atividade[] = [];
    produto2_idVigencia: number = 3;
    produto2_valor: number = 0;


    public get produto1_municipioTodos(): string {
        return this._produto1_municipioTodos;
    }
    public set produto1_municipioTodos(v: string) {
        this._produto1_municipioTodos = v;
        this.alteracao_produto1_municipioTodos.next(v);
    }
    public get produto1_municipioSelecionados(): Municipio[] {
        return this._produto1_municipioSelecionados;
    }
    public set produto1_municipioSelecionados(v: Municipio[]) {
        this._produto1_municipioSelecionados = v;
        this.alteracao_produto1_municipio.next(v);
    }
    public get produto1_idVigencia(): number {
        return this._produto1_idVigencia;
    }
    public set produto1_idVigencia(v: number) {
        this._produto1_idVigencia = v;
        this.alteracao_produto1_idVigencia.next(v);
    }

    public get produto2_ufTodos(): string {
        return this._produto2_ufTodos;
    }
    public set produto2_ufTodos(v: string) {
        this._produto2_ufTodos = v;
        this.alteracao_produto2_ufTodos.next(v);
    }
    public get produto2_ufSelecionados(): UF[] {
        return this._produto2_ufSelecionados;
    }
    public set produto2_ufSelecionados(v: UF[]) {
        this._produto2_ufSelecionados = v;
        this.alteracao_produto2_uf.next(v);
    }
    public get produto2_atividadeTodos(): string {
        return this._produto2_atividadeTodos;
    }
    public get produto2_negociacaoPersonalizada(): boolean {
        return this._produto2_negociacaoPersonalizada;
    }
    public set produto2_atividadeTodos(v: string) {
        this._produto2_atividadeTodos = v;
        this.alteracao_produto2_atividadeTodos.next(v);
    }
    public set produto2_negociacaoPersonalizada(v: boolean) {
        this._produto2_negociacaoPersonalizada = v;
        this.alteracao_produto2_negociacaoPersonalizada.next(v);
    }
    public get produto2_atividadeSelecionados(): Atividade[] {
        return this._produto2_atividadeSelecionados;
    }
    public set produto2_atividadeSelecionados(v: Atividade[]) {
        this._produto2_atividadeSelecionados = v;
        this.alteracao_produto2_atividade.next(v);
    }
}


