import { Component } from '@angular/core';
import { trigger, transition, style, animate, state } from '@angular/animations';
import { Observable } from 'rxjs/Rx';
import * as $ from 'jquery';

@Component({
    selector: 'app-home-banner',
    templateUrl: './_banner.component.html',
    styleUrls: ['./_banner.component.scss'],
    animations: [
        trigger('enterAnimation', [
            transition(':enter', [
                style({ 'opacity': 0 }),
                animate('500ms', style({ 'opacity': 1 }))
            ]),
            transition(':leave', [
                style({ 'opacity': 1 }),
                animate('500ms', style({ 'opacity': 0 }))
            ])
        ])
    ]
})

export class Home_BannerComponent {
    view: number = 1;

    ngOnInit() {
        let timer = Observable.timer(9000, 9000);
        timer.subscribe(t => {
            if (this.view === 3) {
                this.view = 0;
            }

            this.view += 1;
        });
    }

    fnAlterarView(view: number) {
        this.view = view;
    }
}


