import { Component, ViewChild, Output } from '@angular/core';
import { AbstractControl, FormGroup, FormControl, Validators, ValidatorFn, AsyncValidatorFn, ValidationErrors } from '@angular/forms';
import { Router } from '@angular/router';

import { DbService } from '../../../db.service';
import { SessionService } from '../../../session.service';
import { SpinnerService } from '../../../spinner.service';

import { Cadastro } from './cadastro.class';
import { Carrinho, RetornoAssinar } from './carrinho.class';
import { Municipio } from './municipio.class';
import { Produto } from './produto.class';
import { UF } from './uf.class';
import { Atividade } from './atividade.class';
import { Comprovante } from './comprovante.class';

import { AppLoginModalComponent } from '../../app-login/app-login.component'

@Component({
    selector: 'app-sobre-planos',
    templateUrl: './planos.component.html',
    styleUrls: ['./planos.component.scss']
})

export class Sobre_PlanosComponent {
    @ViewChild("login") login: AppLoginModalComponent;

    private url_Cep : string = "https://viacep.com.br/ws/";
    enviado: string = '';
    carrinho        : Carrinho[] = [];
    etapa           : number = 1;
    etapa_erro: number = 0;
    etapa_produto: number = 0;
    etapa_progress: number = 20;
    copy_text: boolean = false;
    form_etapa1: FormGroup;
    form_etapa2_1: FormGroup;
    form_etapa2_1_1: FormGroup;
    form_etapa2_2: FormGroup;
    form_etapa2_2_1: FormGroup;
    form_etapa2_2_2: FormGroup;
    form_etapa3: FormGroup;
    index_produto: number = 0;
    item: Cadastro = new Cadastro();
    idPedido: string = '';
    lista_atividade: Atividade[] = [];
    lista_atividade_filtro: Atividade[] = [];
    lista_municipio: Municipio[] = [];
    lista_municipio_filtro: Municipio[] = [];
    lista_produto: Produto[] = [];
    lista_vigencia_produto1: Array<{ text: string, value: number }> = [
        { text: "1 Mês", value: 1 },
        { text: "6 Meses", value: 2 },
        { text: "12 Meses", value: 3 }
    ];
    lista_vigencia_produto2: Array<{ text: string, value: number }> = [
        { text: "12 Meses", value: 3 }
    ];
    lista_uf: UF[] = [];
    lista_uf_filtro: UF[] = [];
    lista_uf_filtro1: UF[] = [];
    mask: string = "(00) 0000-00009";
    maskDocumento: string = "000.000.000-00";
    progress_step: number = 20;
    retorno_assinar: RetornoAssinar = new RetornoAssinar();
    show_pagamento: boolean = false;
    vigencia_produto1: string = '';
    vigencia_produto2: string = '';
    valor_total: number = 0;
    emailExistente: boolean = false;

    constructor(
        private dbService: DbService,
        private router: Router,
        private sessionService: SessionService,
        private spinnerService: SpinnerService,
    ) {

        this.form_etapa1 = new FormGroup({
            produtos: new FormControl('', Validators.required)
        });

        this.form_etapa2_1 = new FormGroup({
            idVigencia: new FormControl('', Validators.required),
            municipioTodos: new FormControl(''),
            municipioSelecionados: new FormControl('', this.ValidaProduto1())
        });

        this.form_etapa2_1_1 = new FormGroup({
            cdUf: new FormControl('', Validators.required),
            cdMunicipio: new FormControl('', Validators.required)
        });

        this.form_etapa2_2 = new FormGroup({
            ufTodos: new FormControl(''),
            ufSelecionados: new FormControl('', this.ValidaProduto2(1)),
            atividadeTodos: new FormControl(''),
            negociacaoPersonalizada: new FormControl(''),
            atividadeSelecionados: new FormControl('', this.ValidaProduto2(2))
        });

        this.form_etapa2_2_1 = new FormGroup({
            cdUf: new FormControl('', Validators.required)
        });

        this.form_etapa2_2_2 = new FormGroup({
            idAtividade: new FormControl('', Validators.required)
        });

        this.form_etapa3 = new FormGroup({
            pessoal_nmCliente     : new FormControl('', Validators.required),
            pessoal_cdDocumento   : new FormControl('', Validators.required),
            pessoal_nuDocumento   : new FormControl('', this.ValidaCPFCNPJ()),
            pessoal_dsOcupacao    : new FormControl('', Validators.required),
            pessoal_dsOrganizacao : new FormControl('', Validators.required),
            pessoal_dsCEP         : new FormControl('', Validators.required),
            pessoal_dsEndereco    : new FormControl('', Validators.required),
            pessoal_dsNumero      : new FormControl('', Validators.required),
            pessoal_dsComplemento : new FormControl(''),
            pessoal_dsBairro      : new FormControl(''),
            pessoal_dsMunicipio   : new FormControl('', Validators.required),
            pessoal_dsEstado      : new FormControl('', Validators.required),
            pessoal_nuTelefone    : new FormControl('', Validators.required),
            pessoal_dsEmail       : new FormControl('', { validators: [Validators.required, Validators.email], asyncValidators: this.ValidaEmail(), updateOn: 'blur' }),
            pessoal_cdSenha       : new FormControl('', Validators.required),
            pessoal_icTermo       : new FormControl('', this.ValidaTermo())
        });

        // Carrega combos
        this.load();

        // Verifica se houve alteração que necessita cálculo
        this.item.alteracao_produto1_municipioTodos.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto1_municipio.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto1_idVigencia.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto2_ufTodos.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto2_uf.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto2_atividadeTodos.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto2_negociacaoPersonalizada.subscribe(() => {
            this.fnCalcular();
        });
        this.item.alteracao_produto2_atividade.subscribe(() => {
            this.fnCalcular();
        });
        // this.pagamentoService.status.subscribe((status: number) => {
        //     alert('aqui');
        //     // Próxima etapa
        //     this.etapa += 1;
        //     this.etapa_progress += this.progress_step;
        // });

        // Se está autenticado, pula tela de login
        if (this.sessionService.usuario.hash !== '') {
            this.etapa_progress = 25;
            this.progress_step = 25;
            this.item.pessoal_hash = this.sessionService.usuario.hash;
        }

        // Calcula vigência da negocição coletiva
        let data_atual: Date = new Date();
        let data_fim: Date = new Date(data_atual.setMonth(data_atual.getMonth() + 12));
        this.vigencia_produto2 = 
            'Comprando hoje, você terá acesso até ' + 
            (data_fim.getDate() < 10 ? '0' + data_fim.getDate() : data_fim.getDate()) + '/' + 
            ((data_fim.getMonth() + 1) < 10 ? '0' + (data_fim.getMonth() + 1) : (data_fim.getMonth() + 1)) + '/' + 
            data_fim.getFullYear();
    }

    load() {
        this.dbService.get('Produto').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_produto = result;
        });

        this.dbService.get('UF/GetAssinatura').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_uf =
            this.lista_uf_filtro =
            this.lista_uf_filtro1 = result;
        });

        this.dbService.get('Atividade/GetAssinatura').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_atividade =
            this.lista_atividade_filtro = result;
        });
    }

    cmbProduto1Municipio_Changed(value: any): void {
        this.etapa_erro = 0;
    }

    cmbProduto1Municipio_Filter(value) {
        this.lista_municipio_filtro = this.lista_municipio.filter((s) => s.nmMunicipio.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbProduto1UF_Changed(CdUf: number) {
        this.etapa_erro = 0;

        this.dbService.get('Municipio/GetAssinatura/?CdUf=' + CdUf).subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_municipio =
            this.lista_municipio_filtro = result;
        });
    }

    cmbProduto1UF_Filter(value) {
        this.lista_uf_filtro = this.lista_uf.filter((s) => s.nmUf.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbProduto1Vigencia_Change(value) {
        let data_atual: Date = new Date();
        let data_fim: Date = null;

        switch (value)
        {
            case 1: data_fim = new Date(data_atual.setMonth(data_atual.getMonth() + 1)); break;
            case 2: data_fim = new Date(data_atual.setMonth(data_atual.getMonth() + 6)); break;
            case 3: data_fim = new Date(data_atual.setMonth(data_atual.getMonth() + 12)); break;
        }

        if (data_fim !== null) {
            this.vigencia_produto1 = 
                'Comprando hoje, você terá acesso até ' + 
                (data_fim.getDate() < 10 ? '0' + data_fim.getDate() : data_fim.getDate()) + '/' + 
                ((data_fim.getMonth() + 1) < 10 ? '0' + (data_fim.getMonth() + 1) : (data_fim.getMonth() + 1)) + '/' + 
                data_fim.getFullYear();
        } else {
            this.vigencia_produto1 = '';
        }
    }

    cmbProduto2Atividade_Changed(value: any): void {
        this.etapa_erro = 0;
    }

    cmbProduto2Atividade_Filter(value) {
        this.lista_atividade_filtro = this.lista_atividade.filter((s) => s.dsAtividade.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbProduto2UF_Changed(value: any): void {
        this.etapa_erro = 0;
    }

    cmbProduto2UF_Filter(value) {
        this.lista_uf_filtro1 = this.lista_uf.filter((s) => s.nmUf.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    fnCalcular() {
        let temp = {
            produto_selecionado: this.item.produto_selecionado,

            produto1_municipioTodos: this.item.produto1_municipioTodos,
            produto1_municipioSelecionados: this.item._produto1_municipioSelecionados,
            produto1_idVigencia: this.item._produto1_idVigencia,
            
            produto2_ufTodos: this.item.produto2_ufTodos,
            produto2_ufSelecionados: this.item._produto2_ufSelecionados,
            produto2_atividadeTodos: this.item.produto2_atividadeTodos,
            produto2_negociacaoPersonalizada: this.item.produto2_negociacaoPersonalizada,
            produto2_atividadeSelecionados: this.item._produto2_atividadeSelecionados,
            produto2_idVigencia: this.item.produto2_idVigencia
        };

        this.dbService.post('Assinatura/Calcular', temp).subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            if (result.confirmacao === true) {
                this.item.produto1_valor = result.valor_produto1;
                this.item.produto2_valor = result.valor_produto2;
            }
        });
    }

    fnCopyText(){
        var content = (document.getElementById('chave_pix') as HTMLInputElement).value;
        //navigator.clipboard.writeText(content);
        this.copy_text = true;
        return;
    }

    fnEtapaAvancar() {
        // Trata etapas

        if (this.emailExistente && this.sessionService.usuario.hash === '')
        {
            this.login.fnShow('planos', this.etapa);
            return;
        }
            
        switch (this.etapa)
        {
            case 1:
                if (this.item.produto_selecionado.length === 0) {
                    for (let ctrl in this.form_etapa1.controls) { this.form_etapa1.controls[ctrl].markAsTouched(); }
                    return;
                } else {
                    for (let ctrl in this.form_etapa1.controls) { 
                        this.form_etapa1.controls[ctrl].markAsPristine();
                        this.form_etapa1.controls[ctrl].markAsUntouched();
                    }
                }
                break;
            case 2:
                this.form_etapa2_1.controls['municipioSelecionados'].updateValueAndValidity();
                if (this.etapa_produto === 1) {
                    if (!this.form_etapa2_1.valid) {
                        for (let ctrl in this.form_etapa2_1.controls) { this.form_etapa2_1.controls[ctrl].markAsTouched(); }
                        return;
                    } else {
                        var posicao = this.carrinho.findIndex((s) => s.idProduto === this.etapa_produto);

                        if (posicao >= 0) {
                            this.carrinho.splice(posicao, 1);
                        }

                        let _carrinho = new Carrinho();
                        _carrinho.idProduto = this.etapa_produto;
                        _carrinho.dsProduto = this.lista_produto.filter((s) => s.idProduto === this.etapa_produto)[0].dsProduto;
                        _carrinho.valor = this.item.produto1_valor;

                        this.carrinho.push(_carrinho);
                    }
                }

                this.form_etapa2_2.controls['ufSelecionados'].updateValueAndValidity();
                this.form_etapa2_2.controls['atividadeSelecionados'].updateValueAndValidity();
                if (this.etapa_produto === 2) {
                    if (!this.form_etapa2_2.valid) {
                        for (let ctrl in this.form_etapa2_2.controls) { this.form_etapa2_2.controls[ctrl].markAsTouched(); }
                        return;
                    } else {
                        var posicao = this.carrinho.findIndex((s) => s.idProduto === this.etapa_produto);

                        if (posicao >= 0) {
                            this.carrinho.splice(posicao, 1);
                        }

                        let _carrinho = new Carrinho();
                        _carrinho.idProduto = this.etapa_produto;
                        _carrinho.dsProduto = this.lista_produto.filter((s) => s.idProduto === this.etapa_produto)[0].dsProduto;
                        _carrinho.valor = this.item.produto2_valor;

                        this.carrinho.push(_carrinho);
                    }
                }

                this.valor_total = this.item.produto1_valor + this.item.produto2_valor;

                break;
            case 3:
                if (!this.form_etapa3.valid) {
                    for (let ctrl in this.form_etapa3.controls) { this.form_etapa3.controls[ctrl].markAsTouched(); }
                    return;
                }
                break;
        }

        // Verifica se deve mover ainda dentro da etapa 2
        if (this.etapa === 2 && this.index_produto < this.item.produto_selecionado.length) {
            this.etapa_produto = this.item.produto_selecionado[this.index_produto];
            this.index_produto += 1;
            return;
        }

        // Próxima etapa
        this.etapa += 1;
        this.etapa_progress += this.progress_step;

        // Trata etapas
        if (this.etapa === 2) {
            this.etapa_produto = this.item.produto_selecionado[0];
            this.index_produto = 1;
            this.fnCalcular();
        }
        if (this.etapa === 3) {
            // Se está autenticado, pula tela de login
            if (this.sessionService.usuario.hash !== '') {
                this.etapa += 1;
            }
        }

        var customizavel = this.item.produto_selecionado[0];

        if (this.etapa === 4) {
            // Cadastra cliente
            let temp = {
                pessoal_hash          : this.item.pessoal_hash,
                pessoal_nmCliente     : this.item.pessoal_nmCliente,
                pessoal_cdDocumento   : this.item.pessoal_cdDocumento,
                pessoal_nuDocumento   : this.item.pessoal_nuDocumento,
                pessoal_dsOcupacao    : this.item.pessoal_dsOcupacao,
                pessoal_dsOrganizacao : this.item.pessoal_dsOrganizacao,
                pessoal_dsCEP         : this.item.pessoal_dsCEP,
                pessoal_dsEndereco    : this.item.pessoal_dsEndereco,
                pessoal_dsNumero      : this.item.pessoal_dsNumero,
                pessoal_dsComplemento : this.item.pessoal_dsComplemento,
                pessoal_dsBairro      : this.item.pessoal_dsBairro,
                pessoal_dsMunicipio   : this.item.pessoal_dsMunicipio,
                pessoal_dsEstado      : this.item.pessoal_dsEstado,
                pessoal_nuTelefone    : this.item.pessoal_nuTelefone,
                pessoal_dsEmail       : this.item.pessoal_dsEmail,
                pessoal_cdSenha       : this.item.pessoal_cdSenha,
                produto_selecionado   : this.item.produto_selecionado,
    
                produto1_municipioTodos: this.item.produto1_municipioTodos,
                produto1_municipioSelecionados: this.item._produto1_municipioSelecionados,
                produto1_idVigencia: this.item._produto1_idVigencia,
                
                produto2_ufTodos: this.item.produto_selecionado[0] === 3? '1' : this.item.produto2_ufTodos,
                produto2_ufSelecionados: this.item._produto2_ufSelecionados,
                produto2_atividadeTodos: this.item.produto_selecionado[0] === 3? '1' : this.item.produto2_atividadeTodos,
                produto2_atividadeSelecionados: this.item._produto2_atividadeSelecionados,

                produto2_negociacaoPersonalizada: this.item.produto2_negociacaoPersonalizada,
                
                produto2_idVigencia: this.item.produto2_idVigencia
            };

            this.dbService.post('Assinatura/Assinar', temp).subscribe((result: RetornoAssinar) => {
                // Encerra spinner
                this.spinnerService.stop();

                this.retorno_assinar = result;

                if (this.retorno_assinar.confirmacao === true) {
                    // Libera Cielo            
                    var posicao = this.carrinho.findIndex((s) => s.idProduto === this.etapa_produto);

                        if (posicao >= 0) {
                            this.carrinho.splice(posicao, 1);
                        }

                        let _carrinho = new Carrinho();
                        _carrinho.idProduto = this.etapa_produto;
                        _carrinho.dsProduto = this.lista_produto.filter((s) => s.idProduto === this.etapa_produto)[0].dsProduto;
                        
                        if (this.item.produto2_valor <= 0){
                            _carrinho.valor = this.item.produto1_valor
                        } else {
                            _carrinho.valor = this.item.produto2_valor;
                        }
                        

                        this.carrinho.push(_carrinho);
                        this.idPedido = this.retorno_assinar.idPedido;

                    this.show_pagamento = true;
                }
            });
        }
    }

    fnEtapaVoltar() {
        this.etapa -= 1;
        this.etapa_progress -= this.progress_step;

        if (this.etapa === 1) {
            this.carrinho = [];
            this.item.produto_selecionado = [];
            this.etapa_produto = 0;
            this.index_produto = 0
        }

        // Trata etapas
        if (this.etapa === 2) {
            this.etapa_produto = this.item.produto_selecionado[0];
            this.index_produto = 1;
        }
    }

    fnProduto1Adicionar_Municipio() {
        // Valida formulário
        if (!this.form_etapa2_1_1.valid) {
            // Exibe erros
            for (let ctrl in this.form_etapa2_1_1.controls) { this.form_etapa2_1_1.controls[ctrl].markAsTouched(); }
        } else {
            // Verifica se já existe
            var posicao = this.item.produto1_municipioSelecionados.findIndex((s) => s.cdMunicipio === this.item.produto1_cdMunicipio);

            if (posicao >= 0) {
                this.etapa_erro = 3;
                return;
            } else {
                this.etapa_erro = 0;
            }

            // Insere item
            var municipio = this.lista_municipio.filter((s) => s.cdMunicipio === this.item.produto1_cdMunicipio)[0];

            this.item.produto1_municipioSelecionados.push(municipio);
            this.item.produto1_municipioSelecionados = this.item.produto1_municipioSelecionados.sort((obj1, obj2) => {
                if (obj1.nmMunicipioCompleto > obj2.nmMunicipioCompleto) {
                    return 1;
                }
            
                if (obj1.nmMunicipioCompleto < obj2.nmMunicipioCompleto) {
                    return -1;
                }
            
                return 0;
            });
        }
    }

    fnProduto2Adicionar_Atividade() {
        // Valida formulário
        if (!this.form_etapa2_2_2.valid) {
            // Exibe erros
            for (let ctrl in this.form_etapa2_2_2.controls) { this.form_etapa2_2_2.controls[ctrl].markAsTouched(); }
        } else {
            // Verifica se já existe
            var posicao = this.item.produto2_atividadeSelecionados.findIndex((s) => s.idAtividade === this.item.produto2_idAtividade);

            if (posicao >= 0) {
                this.etapa_erro = 2;
                return;
            } else {
                this.etapa_erro = 0;
            }

            // Insere item
            var atividade = this.lista_atividade.filter((s) => s.idAtividade === this.item.produto2_idAtividade)[0];

            this.item.produto2_atividadeSelecionados.push(atividade);
            this.item.produto2_atividadeSelecionados = this.item.produto2_atividadeSelecionados.sort((obj1, obj2) => {
                if (obj1.dsAtividade > obj2.dsAtividade) {
                    return 1;
                }
            
                if (obj1.dsAtividade < obj2.dsAtividade) {
                    return -1;
                }
            
                return 0;
            });
        }
    }

    fnProduto2Adicionar_UF() {
        // Valida formulário
        if (!this.form_etapa2_2_1.valid) {
            // Exibe erros
            for (let ctrl in this.form_etapa2_2_1.controls) { this.form_etapa2_2_1.controls[ctrl].markAsTouched(); }
        } else {
            // Verifica se já existe
            var posicao = this.item.produto2_ufSelecionados.findIndex((s) => s.cdUf === this.item.produto2_cdUf);

            if (posicao >= 0) {
                this.etapa_erro = 1;
                return;
            } else {
                this.etapa_erro = 0;
            }

            // Insere item
            var uf = this.lista_uf.filter((s) => s.cdUf === this.item.produto2_cdUf)[0];

            this.item.produto2_ufSelecionados.push(uf);
            this.item.produto2_ufSelecionados = this.item.produto2_ufSelecionados.sort((obj1, obj2) => {
                if (obj1.nmUf > obj2.nmUf) {
                    return 1;
                }
            
                if (obj1.nmUf < obj2.nmUf) {
                    return -1;
                }
            
                return 0;
            });
        }
    }

    fnProduto1Remover_Municipio(cdMunicipio: number) {
        this.etapa_erro = 0;
        this.item.produto1_municipioSelecionados = this.item.produto1_municipioSelecionados.filter((s) => s.cdMunicipio !== cdMunicipio);
    }

    fnProduto2Remover_Atividade(idAtividade: number) {
        this.etapa_erro = 0;
        this.item.produto2_atividadeSelecionados = this.item.produto2_atividadeSelecionados.filter((s) => s.idAtividade !== idAtividade);
    }

    fnProduto2Remover_UF(cdUf: number) {
        this.etapa_erro = 0;
        this.item.produto2_ufSelecionados = this.item.produto2_ufSelecionados.filter((s) => s.cdUf !== cdUf);
    }

    fnSelecionarProduto(idProduto: number) {

        this.carrinho = [];
        this.item.produto_selecionado = [];
        this.etapa_produto = idProduto;
        this.valor_total = 0;
    
        this.item.produto_selecionado.push(idProduto);

        let _carrinho = new Carrinho();
        _carrinho.idProduto = idProduto;
        _carrinho.dsProduto = this.lista_produto.filter((s) => s.idProduto === idProduto)[0].dsProduto;
        _carrinho.valor = 0;
        this.carrinho.push(_carrinho);

        this.etapa += 1;
        this.fnEtapaAvancar();

    }

    fnVoltar() {
        // Manda para home
        this.router.navigate(['/home']);
    }

    ValidaEmail(): AsyncValidatorFn {
        return (control: AbstractControl): Promise<ValidationErrors | null> => {
            return new Promise (resolve => {

                this.dbService.post('Cliente/VerificarEmail', this.item.pessoal_dsEmail).subscribe(result => {
                    // Encerra spinner
                    this.spinnerService.stop();

                    if (result.emailDisponivel === true) {
                        resolve(null); 
                    } else {
                        this.emailExistente = true;
                        this.login.fnShow('planos', this.etapa);
                        //resolve({"duplicate": true});
                    }
                });
            });
        };
    }

    fnEmailChanged(etapa: number = 0) {
        const sessao = JSON.parse(sessionStorage.getItem('sessao_usuario'));
        if (sessao !== null)
        {
            this.etapa = 2;
            this.fnEtapaAvancar();

        } 
    }

    ValidaCPFCNPJ(): ValidatorFn {
        return (control: AbstractControl) => {

            var strCPFCNPJ = this.item.pessoal_nuDocumento.toString() ;
            var strCPFCNPJ = strCPFCNPJ.replace(/[^\d]+/g,'');

            if (strCPFCNPJ.length == 11)
            {
                var soma;
                var resto;
                soma = 0;

                if (strCPFCNPJ == "00000000000" || 
                    strCPFCNPJ == "11111111111" || 
                    strCPFCNPJ == "22222222222" || 
                    strCPFCNPJ == "33333333333" || 
                    strCPFCNPJ == "44444444444" || 
                    strCPFCNPJ == "55555555555" || 
                    strCPFCNPJ == "66666666666" || 
                    strCPFCNPJ == "77777777777" || 
                    strCPFCNPJ == "88888888888" || 
                    strCPFCNPJ == "99999999999")
                    return { invalido: true }
                
                for (var i=1; i<=9; i++) soma = soma + parseInt(strCPFCNPJ.substring(i-1, i)) * (11 - i);
                
                resto = (soma * 10) % 11;
                    
                if ((resto == 10) || (resto == 11))  resto = 0;
                if (resto != parseInt(strCPFCNPJ.substring(9, 10)) ) return { invalido: true }
                    
                soma = 0;
                for (i = 1; i <= 10; i++) soma = soma + parseInt(strCPFCNPJ.substring(i-1, i)) * (12 - i);
                resto = (soma * 10) % 11;
                    
                if ((resto == 10) || (resto == 11))  resto = 0;
                if (resto != parseInt(strCPFCNPJ.substring(10, 11) ) ) return { invalido: true }

                return null;
            

            } else  {

                if (strCPFCNPJ == "00000000000000" || 
                    strCPFCNPJ == "11111111111111" || 
                    strCPFCNPJ == "22222222222222" || 
                    strCPFCNPJ == "33333333333333" || 
                    strCPFCNPJ == "44444444444444" || 
                    strCPFCNPJ == "55555555555555" || 
                    strCPFCNPJ == "66666666666666" || 
                    strCPFCNPJ == "77777777777777" || 
                    strCPFCNPJ == "88888888888888" || 
                    strCPFCNPJ == "99999999999999")
                    return { invalido: true }
                
                if (!strCPFCNPJ) return { invalido: true }

                // Aceita receber o valor como string, número ou array com todos os dígitos
                const isString = typeof strCPFCNPJ === 'string';
                const validTypes = isString;// || Number.isInteger(strCPFCNPJ) || Array.isArray(strCPFCNPJ);

                // Elimina valor em formato inválido
                if (!validTypes) return { invalido: true }

                // Filtro inicial para entradas do tipo string
                if (isString) {
                    // Limita ao máximo de 18 caracteres, para CNPJ formatado
                    if (strCPFCNPJ.length > 14) return { invalido: true }

                    // Teste Regex para veificar se é uma string apenas dígitos válida
                    const digitsOnly = /^\d{14}$/.test(strCPFCNPJ)
                    // Teste Regex para verificar se é uma string formatada válida
                    const validFormat = /^\d{2}.\d{3}.\d{3}\/\d{4}-\d{2}$/.test(strCPFCNPJ)

                    // Se o formato é válido, usa um truque para seguir o fluxo da validação
                    if (digitsOnly || validFormat) true
                    // Se não, retorna inválido
                    else return { invalido: true }
                }

                // Guarda um array com todos os dígitos do valor
                const match = strCPFCNPJ.toString().match(/\d/g)
                const numbers = Array.isArray(match) ? match.map(Number) : []

                // Valida a quantidade de dígitos
                if (numbers.length !== 14) return { invalido: true }
                
                // Elimina inválidos com todos os dígitos iguais
                const items = Array.from(numbers);
                if (items.length === 1) return { invalido: true }

                // Cálculo validador
                const calc = (x) => {
                    const slice = numbers.slice(0, x)
                    let factor = x - 7
                    let sum = 0

                    for (let i = x; i >= 1; i--) {
                    const n = slice[x - i]
                    sum += n * factor--
                    if (factor < 2) factor = 9
                    }

                    const result = 11 - (sum % 11)

                    return result > 9 ? 0 : result
                }

                // Separa os 2 últimos dígitos de verificadores
                const digits = numbers.slice(12)
                
                // Valida 1o. dígito verificador
                const digit0 = calc(12)
                if (digit0 !== digits[0]) return { invalido: true }

                // Valida 2o. dígito verificador
                const digit1 = calc(13)
                if (digit1 !== digits[1]) return { invalido: true }
                    
                return null;
            } 
        }
    }

    ValidaProduto1(): ValidatorFn {
        return (control: AbstractControl): {[key: string]: any} => {
            if (this.item.produto1_municipioTodos === '1') {
                return null;
            } else if (this.item.produto1_municipioTodos === '0' && (this.item.produto1_municipioSelecionados === null || this.item.produto1_municipioSelecionados.length === 0)) {
                return { invalido: true }
            }
    
            return null;
        };
    }

    ValidaProduto2(campo: number): ValidatorFn {
        return (control: AbstractControl): {[key: string]: any} => {
            switch (campo)
            {
                case 1:
                    if (this.item.produto2_ufTodos === '1') {
                        return null;
                    } else if (this.item.produto2_ufTodos === '0' && (this.item.produto2_ufSelecionados === null || this.item.produto2_ufSelecionados.length === 0)) {
                        return { invalido: true }
                    }
            
                    return null;

                case 2:
                    if (this.item.produto2_atividadeTodos === '1') {
                        return null;
                    } else if (this.item.produto2_atividadeTodos === '0' && (this.item.produto2_atividadeSelecionados === null || this.item.produto2_atividadeSelecionados.length === 0)) {
                        return { invalido: true }
                    }
            
                    return null;
            }
        };
    }

    ValidaTermo(): ValidatorFn {
        return (control: AbstractControl): {[key: string]: any} => {
            if (this.item.pessoal_icTermo === true) {
                return null;
            } else {
                return { invalido: true }
            }
        };
    }

    ValorCielo(valor: number): string {
        return valor.toFixed(2).toString().replace(".", "");
    }

    cbxDocumento_Changed() {
        if (this.item.pessoal_cdDocumento === '1') {
            this.maskDocumento = "000.000.000-00";
        } else {
            this.maskDocumento = "00.000.000/0000-00";
        }
    }

    

    tbxTelefone_Blur() {
        if (this.item.pessoal_nuTelefone.trim().length === 10) {
            this.item.pessoal_nuTelefone += " ";
        }
    }

    tbxTelefone_Changed() {
        if (this.item.pessoal_nuTelefone.trim().length === 11) {
            this.mask = "(00) 00000-0009";
        } else {
            this.mask = "(00) 0000-00009";
        }
    }


   /* ===========================================================================================
    *  Edinaldo FIPE
    *  Dez/2020 
    *  Pesquisa o Logradouro pelo CEP
    ===========================================================================================*/
    fnCep(cep:string){
        cep = cep.replace(/ /gi , '');
        cep = cep.replace(/\-/gi, '');

        if (cep.length == 8) 
        {
            var sUrl = this.url_Cep + cep + '/json/'; 
            this.getCEPs(sUrl);
        }
    }    
    private getCEPs(url) {
        fetch(url)
          .then(res => res.json())
          .then(json => this.showContent(json))
      }
      
    private showContent(cep) {
        this.item.pessoal_dsCEP         = cep.cep;
        this.item.pessoal_dsEndereco    = cep.logradouro;
        this.item.pessoal_dsNumero      = '';
        this.item.pessoal_dsComplemento = '';
        this.item.pessoal_dsBairro      = cep.bairro;
        this.item.pessoal_dsMunicipio   = cep.localidade;
        this.item.pessoal_dsEstado      = cep.uf;
    }

    fnAddFile(){
        var input = (document.getElementById('control_comprovante') as HTMLInputElement);
        var arquivo = input.files[0];

        //if arquivo = 

        const fd = new FormData();
        fd.append(arquivo.name, arquivo); 
        fd.append("idPedido", this.retorno_assinar.idPedido)
            
        this.dbService.postFile("Assinatura/EnviaEmailComprovante", fd).subscribe((result: Comprovante) => {
            this.spinnerService.stop();

                this.enviado = 'ok'; 
        },
                err => {
                this.enviado = 'nok';

        });

    }

    fnChangeFile(){
        var input = (document.getElementById('control_comprovante') as HTMLInputElement);
        var arquivo = input.files[0];    
        document.getElementById('comprovante_texto').innerHTML = '<b style="color: #20a1fa;">' + arquivo.name + '</b>';
    }
}


