import { Component } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { DbService } from '../../../db.service';
import { SpinnerService } from '../../../spinner.service';

import { Cadastrar } from './cadastrar.class'

@Component({
    selector: 'app-sobre-boletim-cadastrar',
    templateUrl: './cadastrar.component.html',
    styleUrls: ['./cadastrar.component.scss']
})

export class Sobre_BoletimCadastrarComponent {
    form: FormGroup;
    item: Cadastrar = new Cadastrar();
    showConfirmacao: boolean = false;

    constructor(
        private dbService: DbService,
        private spinnerService: SpinnerService
    ) {
        this.form = new FormGroup({
            nmPessoa: new FormControl('', Validators.required),
            dsEmail: new FormControl('', [Validators.required, Validators.email]),
            dsOcupacao: new FormControl('', Validators.required),
            dsOrganizacao: new FormControl('', Validators.required)
        });
    }

    fnCadastrar() {
        this.showConfirmacao = false;

        // Valida formulário
        if (!this.form.valid) {
            // Exibe erros
            for (let ctrl in this.form.controls) { this.form.controls[ctrl].markAsTouched(); }
        } else {
            this.dbService.post('Boletim/Cadastrar', this.item).subscribe(result => {
                // Encerra spinner
                this.spinnerService.stop();

                // Exibe notificação
                if (result.confirmacao === true) {
                    this.showConfirmacao = true;

                    // Limpa campos
                    this.item.nmPessoa = '';
                    this.item.dsEmail = '';
                    this.item.dsOcupacao = '';
                    this.item.dsOrganizacao = '';

                    // Limpa erros
                    this.form.reset();
                }
            });
        }
    }
}


