// Módulos Angular
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from '@angular/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgModule, LOCALE_ID } from '@angular/core';
import { registerLocaleData } from '@angular/common';
import localeBr from '@angular/common/locales/pt';
registerLocaleData(localeBr, 'pt');

// Módulos diversos
import { AppRoutingModule } from './app-routing.module';

// Módulos Bootstrap
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { CarouselModule } from 'ngx-bootstrap/carousel';
import { ModalModule } from 'ngx-bootstrap/modal';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';

// Módulos Telerik
import { DialogsModule } from '@progress/kendo-angular-dialog';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { LayoutModule } from '@progress/kendo-angular-layout';

// Serviços
import { DbService } from './db.service';
import { SessionService } from './session.service';
import { SpinnerService } from './spinner.service';

// MasterPage
import { AppComponent } from './components/app/app.component';
import { AppHeaderComponent } from './components/app-header/app-header.component';
import { AppFooterComponent } from './components/app-footer/app-footer.component';
import { AppLoginModalComponent } from './components/app-login/app-login.component';

// Demais componentes
import { AreaAssinanteComponent } from './components/area-assinante/area-assinante.component';
import { HomeComponent } from './components/home/home.component';
import { Home_AssinaturaComponent } from './components/home/_assinatura.component';
import { Home_BannerComponent } from './components/home/_banner.component';
import { Home_BoletinsComponent } from './components/home/_boletins.component';
import { Home_ContatoComponent } from './components/home/_contato.component';
import { Home_ConteudosComponent } from './components/home/_conteudos.component';
import { Home_IndicacaoAmigoComponent } from './components/home/_indicacao-amigo.component';
import { Home_NoticiasComponent } from './components/home/_noticias.component';
import { IndicadoresMercadoComponent } from './components/indicadores-mercado/indicadores-mercado.component';
import { PerfilContratadosComponent } from './components/perfil-contratados/perfil-contratados.component';

import { SolicitacoesEspeciaisComponent } from './components/solicitacoes-especiais/solicitacoes-especiais.component';
import { NegociacaoColetivaDemoComponent } from './components/negociacao-coletiva-geral-demo/negociacao-coletiva-geral-demo.component';
import { NegociacaoColetivaComponent } from './components/negociacao-coletiva-geral/negociacao-coletiva-geral.component';
import { NegociacaoColetivaBloqueioAssinaturaComponent } from './components/negociacao-coletiva-geral/bloqueio-assinatura/bloqueio-assinatura.component';
import { NegociacaoColetivaNewComponent } from './components/negociacao-coletiva-customizavel/negociacao-coletiva-customizavel.component';
import { NegociacaoColetivaNewBloqueioAssinaturaComponent } from './components/negociacao-coletiva-customizavel/bloqueio-assinatura/bloqueio-assinatura.component';
import { PesquisaSalarialDemoComponent } from './components/pesquisa-salarial-demo/pesquisa-salarial-demo.component';
import { PesquisaSalarialComponent } from './components/pesquisa-salarial/pesquisa-salarial.component';
import { PesquisaSalarialBloqueioAssinaturaComponent } from './components/pesquisa-salarial/bloqueio-assinatura/bloqueio-assinatura.component';

import { Sobre_BoletimComponent } from './components/sobre-salariometro/boletim/boletim.component';
import { Sobre_BoletimCadastrarComponent } from './components/sobre-salariometro/boletim/cadastrar.component';
import { Sobre_MetodologiaComponent } from './components/sobre-salariometro/metodologia/metodologia.component';
import { Sobre_PlanosComponent } from './components/sobre-salariometro/planos/planos.component';
import { Sobre_QuemSomosComponent } from './components/sobre-salariometro/quem-somos/quem-somos.component';
import { NotFound404Component } from './components/not-found-404/not-found-404.component';
import { EmConstrucaoComponent } from './components/em-construcao/em-construcao.component';

// Pipes
import { FirstErrorPipe } from './first-error.pipe';
import { MunicipioFilterPipe } from './components/sobre-salariometro/planos/municipio-filter.pipe';
import { AppRecuperarSenhaComponent } from './components/app-recuperar-senha/app-recuperar-senha.component';

@NgModule({
    declarations: [
        AppComponent,
        AppHeaderComponent,
        AppFooterComponent,
        AppLoginModalComponent,
        AppRecuperarSenhaComponent,
        AreaAssinanteComponent,
        HomeComponent,
        Home_AssinaturaComponent,
        Home_BannerComponent,
        Home_BoletinsComponent,
        Home_ContatoComponent,
        Home_ConteudosComponent,
        Home_IndicacaoAmigoComponent,
        Home_NoticiasComponent,
        IndicadoresMercadoComponent,
        SolicitacoesEspeciaisComponent,
        NegociacaoColetivaDemoComponent,
        NegociacaoColetivaComponent,
        NegociacaoColetivaBloqueioAssinaturaComponent,
        NegociacaoColetivaNewComponent,
        NegociacaoColetivaNewBloqueioAssinaturaComponent,
        PerfilContratadosComponent,
        PesquisaSalarialBloqueioAssinaturaComponent,
        PesquisaSalarialDemoComponent,
        PesquisaSalarialComponent,
        Sobre_BoletimComponent,
        Sobre_BoletimCadastrarComponent,
        Sobre_MetodologiaComponent,
        Sobre_PlanosComponent,
        Sobre_QuemSomosComponent,
        NotFound404Component,
        EmConstrucaoComponent,
        FirstErrorPipe,
        MunicipioFilterPipe,
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        HttpModule,
        FormsModule,
        ReactiveFormsModule,
        AppRoutingModule,
        BsDropdownModule.forRoot(),
        CarouselModule.forRoot(),
        ModalModule.forRoot(),
        ScrollToModule.forRoot(),
        DialogsModule,
        DropDownsModule,
        InputsModule,
        LayoutModule
    ],
    providers: [
        DbService,
        SessionService,
        SpinnerService,
        { provide: LOCALE_ID, useValue: 'pt' }
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }


