import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { SessionService } from './session.service';
import { SpinnerService } from './spinner.service';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { HttpHeaders } from '@angular/common/http';



@Injectable()
export class DbService {

    private url_services  : string = window.location.origin.indexOf('localhost') !== -1 ? "http://localhost:55329/api/" : "/service/api/";
    private   url_Cep     : string = "https://";
      
    exibe_spinner: boolean = true;

    constructor(
        private http: Http,
        private sessionService: SessionService,
        private spinnerService: SpinnerService,
        private router: Router,
    ) { }

    private httpOptionsCep = {
        headers: new Headers({
            'Content-Type': 'application/json'
        })
    };

    get(url: string) {
        if (this.exibe_spinner) {
            this.spinnerService.start();
        }

        return this.http.get(this.url_services + url)
            .map(this.extractData);
    }

    post(url: string, parametros: any) {

        if (this.exibe_spinner) {
            this.spinnerService.start();
        }

        let headers = new Headers({ 'Content-Type': 'application/json', 'Hash': this.sessionService.usuario.hash });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.url_services + url, JSON.stringify(parametros), options)
            .map(this.extractData)
            .catch((err) => {

                if(err.status == 401){
                    this.router.navigate(['/home']);
                    this.sessionService.sessaoDupla = true;
                }

                return Observable.throw(err)                
            });
    }

    postFile(url: string, parametros: any): Observable<{}> {

        
        let headers = new Headers({ 'Accept': 'application/json', 'Hash': this.sessionService.usuario.hash }, );
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.url_services + url, parametros, options)
            .catch((err) => {
                if (err.status === 401) {
                    this.router.navigate(['/login']);
                    this.sessionService.sessaoDupla = true;
                }
                return Observable.throw(err);
            });
    }

    put(url: string, parametros: any) {
        if (this.exibe_spinner) {
            this.spinnerService.start();
        }

        let headers = new Headers({ 'Content-Type': 'application/json', 'Hash': this.sessionService.usuario.hash });
        let options = new RequestOptions({ headers: headers });

        return this.http.put(this.url_services + url, JSON.stringify(parametros), options).map(this.extractData);
    }

    delete(url: string) {
        if (this.exibe_spinner) {
            this.spinnerService.start();
        }

        return this.http.delete(this.url_services + url)
            .map(this.extractData);
    }

    private extractData(res: Response) {
        let body;

        // check if empty, before call json
        if (res.text()) {
            body = res.json();
        }

        return body || {};
    }

}


