import { Component } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as $ from 'jquery';

import { AppLoginModalComponent } from '../app-login/app-login.component'

import { DbService } from '../../db.service';
import { SessionService } from '../../session.service';
import { SpinnerService } from '../../spinner.service';

import { Filtro } from './filtro.class';
import { CBO } from './cbo.class';
import { UF } from './uf.class';
import { FaixaEtaria } from './faixa-etaria.class';
import { Genero } from './genero.class';
import { Cor } from './cor.class';
import { Escolaridade } from './escolaridade.class';
import { PerfilContratados } from './perfil-contratados.class';
import { ScrollToConfigOptions, ScrollToService } from '@nicky-lenaers/ngx-scroll-to';

@Component({
    selector: 'app-perfil-contratados-root',
    templateUrl: './perfil-contratados.component.html',
    styleUrls: ['./perfil-contratados.component.scss']
})

export class PerfilContratadosComponent {
    atualizacaoInicio: string = '';
    atualizacaoFim: string = '';
    dados: PerfilContratados = new PerfilContratados();
    dados_cbo: string = '';
    dados_salario: string = '';
    dados_qtde: string = '';
    filtro: Filtro = new Filtro();
    form: FormGroup;
    lista_cbo: CBO[] = [];
    lista_cbo_filtro: CBO[] = [];
    lista_uf: UF[] = [];
    lista_uf_filtro: UF[] = [];
    lista_faixa: FaixaEtaria[] = [];
    lista_faixa_filtro: FaixaEtaria[] = [];
    lista_genero: Genero[] = [];
    lista_genero_filtro: Genero[] = [];
    lista_cor: Cor[] = [];
    lista_cor_filtro: Cor[] = [];
    lista_escolaridade: Escolaridade[] = [];
    lista_escolaridade_filtro: Escolaridade[] = [];
    showDados: boolean = false;
    showNulo: boolean = false;

    textoFiltros: boolean = false;
    textoCBO: string = '';
    textoUf: string = '';
    textoFaixaEtaria: string = '';
    textoGenero: string = '';
    textoCor: string = '';
    textoEscolaridade: string = '';

    constructor(
        private _scrollToService: ScrollToService,
        private dbService: DbService,
        private sessionService: SessionService,
        private spinnerService: SpinnerService
    ) {
        this.form = new FormGroup({
            cdCbo: new FormControl('', Validators.required),
            cdUf: new FormControl('', Validators.required),
            cdFaixaEtaria: new FormControl('', Validators.required),
            cdGenero: new FormControl('', Validators.required),
            cdCor: new FormControl('', Validators.required),
            cdEscolaridade: new FormControl('', Validators.required)
        });

        // Carrega combos
        this.combosLoad();

        // Carrega data de atualização
        this.dbService.get('ParametroAtualizacao/?id_transacao=4').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.atualizacaoInicio = result.atualizacaoInicio;
            this.atualizacaoFim = result.atualizacaoFim;
        });
    }

    combosLoad() {
        this.dbService.get('CBO').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_cbo =
            this.lista_cbo_filtro = result;
        });

        this.dbService.get('UF').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_uf = 
            this.lista_uf_filtro = result;
            this.filtro.cdUf = 99;
        });

        this.dbService.get('FaixaEtaria').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_faixa =
            this.lista_faixa_filtro = result;
            this.filtro.cdFaixaEtaria = 9;
        });

        this.dbService.get('Genero').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_genero =
            this.lista_genero_filtro = result;
            this.filtro.cdGenero = 9;
        });

        this.dbService.get('Cor').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_cor =
            this.lista_cor_filtro = result;
            this.filtro.cdCor = 9;
        });

        this.dbService.get('Escolaridade').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_escolaridade =
            this.lista_escolaridade_filtro = result;
            this.filtro.cdEscolaridade = 9;
        });
    }

    cmbCbo_Filter(value) {
        this.lista_cbo_filtro = this.lista_cbo.filter((s) => s.nmCbo.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbUF_Filter(value) {
        this.lista_uf_filtro = this.lista_uf.filter((s) => s.sgUf.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbFaixa_Filter(value) {
        this.lista_faixa_filtro = this.lista_faixa.filter((s) => s.dsFaixaEtaria.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbGenero_Filter(value) {
        this.lista_genero_filtro = this.lista_genero.filter((s) => s.dsGenero.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbCor_Filter(value) {
        this.lista_cor_filtro = this.lista_cor.filter((s) => s.dsCor.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    cmbEscolaridade_Filter(value) {
        this.lista_escolaridade_filtro = this.lista_escolaridade.filter((s) => s.dsEscolaridade.toLowerCase().indexOf(value.toLowerCase()) !== -1);
    }

    fnLimpar() {
        // Limpa campos
        this.filtro.cdCbo = null;
        this.filtro.cdUf = null;
        this.filtro.cdFaixaEtaria = null;
        this.filtro.cdGenero = null;
        this.filtro.cdCor = null;
        this.filtro.cdEscolaridade = null;

        // Limpa erros
        this.form.reset();

        // Esconde tabelas
        $("#valor_salario").text("");
        this.showDados = false;
        this.showNulo = false;
        this.textoFiltros = false;
    }

    fnPesquisar() {
        let label = $("#valor_salario");

        this.showDados = false;
        this.showNulo = false;
        this.textoFiltros = false;
        label.text("");

        // Valida formulário
        if (!this.form.valid) {
            // Exibe erros
            for (let ctrl in this.form.controls) { this.form.controls[ctrl].markAsTouched(); }
        } else {
            this.dbService.post('PerfilContratados', this.filtro).subscribe(result => {
                // Encerra spinner
                this.spinnerService.stop();

                // Rola scroll
                this.fnScrollTo();

                if (!isNaN(result.vlSalario)) {
                    // Guarda objetos
                    this.dados_cbo = this.lista_cbo.filter((s) => s.cdCbo === this.filtro.cdCbo)[0].nmCbo;
                    this.dados_salario = result.vlSalario;
                    this.dados_qtde = result.qtde;
                    this.showDados = true;
                    this.textoFiltros = true;

                    this.textoCBO = this.lista_cbo.filter((s) => s.cdCbo === this.filtro.cdCbo)[0].nmCbo;
                    this.textoUf = this.lista_uf.filter((s) => s.cdUf === this.filtro.cdUf)[0].sgUf
                    this.textoFaixaEtaria = this.lista_faixa.filter((s) => s.cdFaixaEtaria === this.filtro.cdFaixaEtaria)[0].dsFaixaEtaria;
                    this.textoGenero = this.lista_genero.filter((s) => s.cdGenero === this.filtro.cdGenero)[0].dsGenero;
                    this.textoCor = this.lista_cor.filter((s) => s.cdCor === this.filtro.cdCor)[0].dsCor;
                    this.textoEscolaridade = this.lista_escolaridade.filter((s) => s.cdEscolaridade === this.filtro.cdEscolaridade)[0].dsEscolaridade;

                    label.text(this.dados_salario);
                    label.prop("Counter", 500).animate({
                        Counter: label.text()
                    }, {
                        duration: 1900,
                        easing: "swing",
                        step: function (now) {
                            label.text(Math.ceil(now).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }).replace(',00', ''));
                        }
                    });
                
                    var audio = new Audio('/assets/perfil.mp3');
                    audio.play();
                } else {
                    this.showNulo = true;
                    this.textoFiltros = false;
                }
            });
        }
    }

    fnScrollTo() {
        const config: ScrollToConfigOptions = {
            target: 'resultado',
            offset: -97
        };
      
        this._scrollToService.scrollTo(config);
    }
}


