import { Component, ViewChild, ElementRef } from '@angular/core';

import { AppLoginModalComponent } from '../app-login/app-login.component'

import { DbService } from '../../db.service';
import { SessionService } from '../../session.service';
import { SpinnerService } from '../../spinner.service';
import { Router, ActivatedRoute } from '@angular/router';

import { ScrollToConfigOptions, ScrollToService } from '@nicky-lenaers/ngx-scroll-to';

declare var powerbi: any;

@Component({
    selector: 'app-solicitacoes-especiais',
    templateUrl: './solicitacoes-especiais.component.html',
    styleUrls: ['./solicitacoes-especiais.component.scss']
})

export class SolicitacoesEspeciaisComponent {
    @ViewChild("login") login: AppLoginModalComponent;
    @ViewChild('embeddedReport')
    
    embeddedReport: ElementRef;
    config: any;

    showBloqueio: boolean = false;
    descricao: string = this.route.snapshot.params['dsRelatorio'];

    constructor(
        private _scrollToService: ScrollToService,
        private dbService: DbService,
        private router: Router,
        private route: ActivatedRoute,
        private sessionService: SessionService,
        private spinnerService: SpinnerService
    ) {
        this.fnPesquisar();
    }

    fnPesquisar() {
        const sessao = JSON.parse(sessionStorage.getItem('sessao_usuario')  || '{}');

        var idRelatorioPB = this.route.snapshot.params['idRelatorioPB'];
        var dsRelatorio = this.route.snapshot.params['dsRelatorio'];


        //this.dbService.post('EmbedInfo/VerificaPermissaoNCP', sessao.hash).subscribe(resultPersonalizado => {
            // if (!resultPersonalizado.permissao)
            // {
                
                // this.dbService.post('EmbedInfo/VerificaSolicitacoesEspeciais', { hash: sessao.hash, relatorio: dsReportId }).subscribe(resultComum => {
                //     if (!resultComum.permissao) 
                //     {
                //         this.fnScrollTo();
                //         this.showBloqueio = true;
                //     } else {

                var url = 'EmbedInfo/GetSolicitacoesEspeciais?idRelatorio=' + idRelatorioPB;

                        this.dbService.get(url).subscribe(result => {
                            var pages = [];
                            const model = window['powerbi-client'].models;
                            const basicFilter = {
                                $schema: "http://powerbi.com/product/schema#basic",
                                target: {
                                  table: "VW_ASSINATURAS_ATIVAS_NC",
                                  column: "IdCliente"
                                },
                                operator: "In",
                                values: [sessao.idUsuario],
                                filterType: model.FilterType.BasicFilter,
                            }
                
                            this.config = result;
                            
                            const embedConfig = {
                                type: 'report',
                                tokenType: model.TokenType.Embed,
                                accessToken: result.embedToken.token,
                                embedUrl: result.embedReport[0].embedUrl,
                                permissions: model.Permissions.All,
                                filters: [basicFilter],
                                settings: {
                                    filterPaneEnabled: false,
                                    navContentPaneEnabled: false,
                                    layoutType: model.LayoutType.Custom,
                                    customLayout: {
                                        displayOption: model.DisplayOption.FitToWidth
                                    }
                                }
                            }
                            var report = powerbi.embed(this.embeddedReport.nativeElement, embedConfig);
                            report.fullscreen();
                        });
            
    }

    fullscreen() {
        // Get a reference to the embedded report HTML element
        var embedContainer = this.embeddedReport.nativeElement

        // Get a reference to the embedded report.
        var report = powerbi.get(embedContainer);

        // Displays the report in full screen mode.
        report.fullscreen();
    }

    fnScrollTo() {
        const config: ScrollToConfigOptions = {
            target: 'resultado',
            offset: -97
        };
      
        this._scrollToService.scrollTo(config);
    }

}


