import { Component } from '@angular/core';

import { DbService } from '../../../db.service';
import { SpinnerService } from '../../../spinner.service';

import { Boletim } from './boletim.class';

@Component({
    selector: 'app-sobre-boletim',
    templateUrl: './boletim.component.html',
    styleUrls: ['./boletim.component.scss']
})

export class Sobre_BoletimComponent {
    ano: number = 0;
    lista_anos: number[] = [];
    lista_boletins: Boletim[] = [];
    url_site: string = 'http://salariometro.fipe.org.br/home';

    constructor(
        private dbService: DbService,
        private spinnerService: SpinnerService
    ) {
        // Carrega lateral
        this.lateralLoad();
    }

    fnAbrirPDF(url: string) {
        window.open('/assets/boletins/pdfs/' + url, '_blank');
    }

    boletinsLoad(ano: number) {
        this.ano = ano;

        this.dbService.post('Boletim/GetByAno', ano).subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_boletins = result;
        });
    }

    lateralLoad() {
        this.dbService.get('Boletim/GetAllAnos').subscribe(result => {
            // Encerra spinner
            this.spinnerService.stop();

            // Guarda objetos
            this.lista_anos = result;

            // Carrega boletins
            this.boletinsLoad(this.lista_anos[0]);
        });
    }

    replaceUrl(s:string) {
        return s.replace(/%/g,'%25');
    }
}


