import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/share';

export class Relatorio {
    idPedidoRelatorio: number = 0;
    idPedido: number = 0;
    dsRelatorio: string = '';
    idRelatorioPB: string = '';
    idCliente: string = '';
    dtVigencia: Date = new Date();
}


export class Usuario {
    hash: string = '';
    nmUsuario: string = '';
    idUsuario: number = 0;
    lstRelatorios: Relatorio[] = [];
    nrRelatorios: number = 0;
}

@Injectable()
export class SessionService {
    public sessao: Subject<Usuario> = new Subject();
    public usuario: Usuario = new Usuario();
    public sessaoDupla: boolean = false;
    public avisoAssinatura: boolean = false;    //EGS 31.12.2020


    constructor(
        private router: Router
    ) {
    }

    fnEncerrarSessao() {
        // Encerra sessão
        sessionStorage.removeItem('sessao_usuario');
        this.fnGetSessao();

        // Manda para tela de login
        this.router.navigate(['/home']);
        location.reload();
    }

    fnGetSessao() {
        const sessao = JSON.parse(sessionStorage.getItem('sessao_usuario'));

        if (sessao !== null) {
            this.usuario.hash = sessao['hash'];
            this.usuario.nmUsuario = sessao['nmUsuario'];
            this.usuario.idUsuario = sessao['idUsuario'];
            this.usuario.lstRelatorios = sessao['lstRelatorios'];
            this.usuario.nrRelatorios = this.usuario.lstRelatorios.length;
        } else {
            this.usuario.hash = '';
            this.usuario.nmUsuario = '';
            this.usuario.idUsuario = 0;
            this.usuario.lstRelatorios = [];
            this.usuario.nrRelatorios = 0;
        }
        this.sessao.next(this.usuario);
    }
    
    fnSetSessao() {
        sessionStorage.setItem('sessao_usuario', JSON.stringify(this.usuario));
        this.sessao.next(this.usuario);
    }
}


